//axios
import axios from 'axios'
import {
    Message
} from 'element-ui'
import qs from 'qs'
const config = require('./config.js')

axios.defaults.baseURL = config.baseUrl
//
axios.defaults.headers = {
    // 'X-Requested-With': 'XMLHttpRequest',
    'Content-Type': 'application/x-www-form-urlencoded'
}
axios.defaults.timeout = 10000

axios.defaults.withCredentials = true;

let cancel, promiseArr = {}
const CancelToken = axios.CancelToken;
//
axios.interceptors.request.use(config => {
    //
    if (promiseArr[config.url]) {
        promiseArr[config.url]('')
        promiseArr[config.url] = cancel
    } else {
        promiseArr[config.url] = cancel
    }
    return config
}, error => {
    return Promise.reject(error)
})

//
axios.interceptors.response.use(response => {
    return response
}, error => {
    if (error && error.response) {
        switch (error.response.status) {
            case 400:
                error.message = ''
                break;
            case 401:
                error.message = ''
                break;
            case 403:
                error.message = ''
                break;
            case 404:
                error.message = ','
                break;
            case 405:
                error.message = ''
                break;
            case 408:
                error.message = ''
                break;
            case 500:
                error.message = ''
                break;
            case 501:
                error.message = ''
                break;
            case 502:
                error.message = ''
                break;
            case 503:
                error.message = ''
                break;
            case 504:
                error.message = ''
                break;
            case 505:
                error.message = 'http'
                break;
            default:
                error.message = `${error.response.status}`
        }
    } else {
        error.message = ""
    }
    Message.error(error.message)
    return Promise.resolve(error.response)
})

const beforeResolve = function (res) {
    const data = res.data
    if (data.errno === 0) res._succ = true
    let msg = {
        type: '',
        tips: ''
    }
    if (data.errno) {
        let arr = []
        console.log(data)
        switch (data.errno) {
            // case 500001:
            //     {
            //         arr = ['error', ``];
            //         break;
            //     };
            default:
                {
                    if (data.msg) {
                        arr = ['error', data.msg]
                    } else {
                        arr = ['error', `\n${data.errno}\n${data.msg}`]
                    }
                }
        }
        msg = {
            type: arr[0],
            tips: arr[1]
        }
    } else {
        msg = {
            type: 'success',
            tips: data.msg || ''
        }
    }
    if (msg.tips) {
        Message[msg.type](msg.tips)
    }
}


export default {
    //get
    get(url, param) {
        return new Promise((resolve, reject) => {
            axios({
                method: 'get',
                url,
                params: param,
                cancelToken: new CancelToken(c => {
                    cancel = c
                })
            }).then(res => {
                beforeResolve(res)
                resolve(res)
            })
        })
    },
    //post
    post(url, param) {
        param = qs.stringify(param)
        return new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url,
                data: param,
                cancelToken: new CancelToken(c => {
                    cancel = c
                })
            }).then(res => {
                beforeResolve(res)
                resolve(res)
            })
        })
    }
}